	module.exports = {"data":{"post":{"html":"<p>Christopher's first day of school may not be until September 2nd, but he'll get his first taste of school this week. As part of the process to determine whether he'll attend regular elementary school or a special ed school, he'll be evaluated for 3 days at the special ed school in Buchholz, this Friday, Monday and Tuesday. He'll be picked up by taxi the three days, and Christopher is as excited about that as he is about going to school.</p>\n<p>Already last Sunday (yes, Sunday!) we had a home visit from one of the evaluators. Her initial impression was that Christopher would be just fine at regular school, but that decision will be made by the county. </p>\n<p>For me as the stay-at-home parent this will be the first taste of the German school schedule, since Christopher will be home at 11:30 instead of the usual 3:15. There are no meals at school, unlike kindergarten where he gets breakfast and lunch. I wonder if they will give him homework as well.</p>\n<p>(Does anybody recognize the title as Grace Kelly's best line from <a href=\"http://www.imdb.com/title/tt0047396/\">Rear Window</a>?)</p>","fields":{"title":"Previews of Coming Attractions","date":"14 Mar 2006","url":"https://papascott.de/2006-03-14-previews-of-coming-attractions/"}}},"pathContext":{"slug":"/2006-03-14-previews-of-coming-attractions/","prev":{"fields":{"title":"Who Turned The Heat Off?","date":"13 Mar 2006","slug":"/2006-03-13-who-turned-the-heat-off/"}},"next":{"fields":{"title":"Messenger Prank","date":"15 Mar 2006","slug":"/2006-03-15-messenger-prank/"}}}}

